import React, { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import useSWR from "swr";
import { fetcher, imgUrl, tmdbAPI } from "apiConfig/config";
import { Swiper, SwiperSlide } from "swiper/react";
import Poster from "components/banner/Poster";
import ModalMovie from "components/modal/ModalMovie";
import SimilarMovie from "components/movie/SimilarMovie";
const MovieDetailsPage = () => {
  const { movieID } = useParams();

  const [videoTrailers, setVideoTrailders] = useState();
  const [openModal, setOpenModal] = useState(false);
  const [type, setType] = useState("Trailer");

  const { data } = useSWR(tmdbAPI?.getMovieDetails(movieID), fetcher);
  const { data: dataVideo } = useSWR(tmdbAPI?.getMovieVideo(movieID), fetcher);
  const { data: actors } = useSWR(
    tmdbAPI?.getMovieInfo(movieID, "credits"),
    fetcher
  );
  useEffect(() => {
    setVideoTrailders(dataVideo?.videos?.results);
  }, [dataVideo?.videos?.results]);

  const handleClickOpenTrailer = (type) => {
    setType(type);
    setOpenModal(true);
  };

  if (!data || !actors) return null;
  const {
    backdrop_path,
    title,
    genres,
    release_date,
    vote_average,
    runtime,
    poster_path,
    overview,
  } = data;

  const { cast } = actors;
  return (
    <>
      <Poster
        backdrop_path={backdrop_path}
        id={movieID}
        title={title}
        genres={genres}
        setOpenModal={() => setOpenModal(true)}
      />
      <div className="flex justify-between items-center py-10 max-w-[975px] mx-auto">
        <div className="text-white">
          <h2 className="font-bold text-[60px] max-w-[500px]">{`${title} ${new Date(
            release_date
          ).getFullYear()}`}</h2>
          <div className="flex items-center gap-4 mt-10">
            <div className="flex items-center gap-3">
              <div
                style={{
                  background: `conic-gradient(#f62682 ${
                    vote_average * 10 * 3.6
                  }deg, rgba(246,38,130,0.3) 0deg)`,
                }}
                className="relative h-[60px] w-[60px] rounded-full"
              >
                <div className="absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 w-[80%] h-[80%] rounded-full bg-black flex items-center justify-center">
                  {vote_average?.toFixed(1)}
                </div>
              </div>
              <span className="text-xl font-medium capitalize">
                User <br /> score
              </span>
            </div>
            <span className="text-3xl">{`${parseInt(runtime / 60)}h ${
              runtime % 60
            }m`}</span>
          </div>
          <div className="flex items-center gap-x-4 mt-10">
            {genres?.map((item) => (
              <span
                key={item.id}
                className="py-2 px-4 border border-white rounded-md capitalize"
              >
                {item.name}
              </span>
            ))}
          </div>
          <div className="flex items-center gap-x-8 mt-10">
            <button
              className="uppercase font-bold text-[#304FFE]"
              onClick={() => {
                handleClickOpenTrailer("Trailer");
              }}
            >
              watch trailer
            </button>
            <button className="py-7 px-9 bg-primary uppercase font-bold rounded-2xl">
              watch full movie
            </button>
          </div>
        </div>
        <div className="w-[400px] h-[550px] rounded-lg overflow-hidden relative">
          <img
            src={`${imgUrl()}${poster_path}`}
            alt=""
            className="w-full h-full object-cover"
          />
          <div
            className="absolute left-1/2 top-1/2 -translate-x-1/2 -translate-y-1/2 cursor-pointer select-none"
            onClick={() => {
              handleClickOpenTrailer("Teaser");
            }}
          >
            <svg
              width="134"
              height="134"
              viewBox="0 0 134 134"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <circle
                cx="67"
                cy="67"
                r="64"
                fill="black"
                fillOpacity="0.6"
                stroke="white"
                strokeWidth="6"
              />
              <path
                fillRule="evenodd"
                clipRule="evenodd"
                d="M57.1449 32.0575L93.0281 62.6575C94.2434 63.6925 95 65.245 95 66.9775C95 68.71 94.2204 70.2625 93.0281 71.2975L57.2137 101.875L56.6175 102.393C56.0443 102.775 55.3564 103 54.6227 103C52.6279 103 51 101.335 51 99.265V34.735C51 32.665 52.6279 31 54.6227 31C55.3794 31 56.0902 31.2475 56.6634 31.6525L57.1449 32.0575Z"
                fill="white"
              />
            </svg>
          </div>
        </div>
        <ModalMovie
          onClose={() => setOpenModal(false)}
          visible={openModal}
          contentClassName="content z-10"
          videos={videoTrailers}
          type={type}
        />
      </div>

      <MovieOverView
        overview={overview}
        characters={actors?.cast}
        crew={actors?.crew}
        cast={cast}
      />
      <SimilarMovie movieID={movieID} />
    </>
  );
};

const MovieOverView = ({ overview, cast }) => {
  return (
    <div className="max-w-[1200px] mx-auto px-[30px]  bg-gradient-to-b from-slate-900 to-[rgba(246,38,130,0.6)] min-h-[1000px] rounded-2xl mt-10 text-white p-10">
      <div className="">
        <h2 className="text-6xl capitalize font-medium ">Overview</h2>
        <p className="mt-10 leading-10 text-xl ">{overview}</p>
      </div>

      <div className="mt-20 select-none">
        <h2 className="text-center uppercase text-6xl mb-10">Cast</h2>
        <Swiper
          className=""
          grabCursor={true}
          spaceBetween={30}
          slidesPerView={4}
          pagination={{
            clickable: true,
            renderBullet: function (index, className) {
              return `<span class=${className}></span>`;
            },
          }}
        >
          {cast?.slice(0, 8).map((item) => (
            <SwiperSlide key={item.id} className="rounded-2xl overflow-hidden">
              <div className="w-full h-[350px]">
                <img
                  
                  src={`${imgUrl()}${item?.profile_path}`}
                  alt=""
                  className="w-full h-full object-cover object-top"
                />
              </div>
              <div className="bg-white text-black p-3">
                <h4 className="text-4xl font-bold mb-2 h-20 name-actor">
                  {item?.original_name}
                </h4>
                <span className="text-2xl font-normal name-character">
                  {item?.character || "Mass actor"}
                </span>
              </div>
            </SwiperSlide>
          ))}
        </Swiper>
      </div>
    </div>
  );
};

export default MovieDetailsPage;
