import React from "react";
import { useNavigate } from "react-router-dom";
import { imgUrl } from "apiConfig/config";
import Button from "components/button/Button";

const MovieCard = ({ movie }) => {
  const { poster_path, title, release_date, vote_average, id } = movie;
  const navigate = useNavigate();
  const handleClickMovieDetail = () => {
    navigate(`/movies/${id}`);
  };
  return (
    <div className="movie-card rounded-lg p-3 bg-slate-800 text-white h-full flex flex-col">
      <div className="cursor-pointer" onClick={handleClickMovieDetail}>
        <img
          onError={(event) => (event.target.style.display = "none")}
          src={`${imgUrl()}${poster_path}`}
          alt=""
          className="w-full h-[250px] object-cover rounded-lg object-top"
        />
      </div>

      <div className="flex flex-col flex-grown">
        <h3 className="text-white text-xl font-bold mb-3 movie-title mt-2 flex">
          {title}
        </h3>
        <div className="flex items-center justify-between text-sm opacity-80 mb-10">
          <span>{new Date(release_date)?.getFullYear()}</span>
          <div className="flex items-center justify-center gap-x-1">
            <span>{vote_average?.toFixed(1)}</span>
            <svg
              width="20"
              height="20"
              viewBox="0 0 46 43"
              fill="none"
              xmlns="http://www.w3.org/2000/svg"
            >
              <path
                fillRule="evenodd"
                clipRule="evenodd"
                d="M46 16.4415H28.4009L23 0L17.5991 16.4415H0L14.3955 26.5585L8.78929 43L23 32.8106L37.2107 43L31.5942 26.5585L46 16.4415Z"
                fill="#FF9500"
              />
            </svg>
          </div>
        </div>

        <Button
          onClick={() => navigate(`/movies/${id}`)}
          type="button"
          className="w-full"
        >
          Watch Now
        </Button>
      </div>
    </div>
  );
};

export default MovieCard;
