import React, { useEffect, useState } from "react";
import { Swiper, SwiperSlide } from "swiper/react";
import { Pagination } from "swiper";
import "swiper/css/pagination";
import MovieCard from "./MovieCard";
import useSWR from "swr";
import { api_key, fetcher, tmdbAPI } from "apiConfig/config";

const MovieList = ({ type, movieID = null, similar = false }) => {
  const [movies, setMovies] = useState([]);
  const [url, setUrl] = useState(null);

  const { data } = useSWR(url, fetcher);
  useEffect(() => {
    setMovies(data?.results);
  }, [data]);

  useEffect(() => {
    if (similar && movieID) {
      setUrl(tmdbAPI.getMovieInfo(movieID, similar));
    }
  }, [movieID, similar]);

  useEffect(() => {
    if (type) {
      setUrl(tmdbAPI.getMovieListType(type));
    }
  }, [type]);

  return (
    <div className="movie-list">
      <Swiper
        modules={[Pagination]}
        spaceBetween={40}
        slidesPerView={4}
        grabCursor={true}
        pagination={{
          clickable: true,
          renderBullet: function (index, className) {
            return `<span class=${className}></span>`;
          },
        }}
      >
        {movies?.map((movie) => (
          <SwiperSlide key={movie.id}>
            <MovieCard movie={movie} api_key={api_key} />
          </SwiperSlide>
        ))}
      </Swiper>
    </div>
  );
};

export default MovieList;
