import React from "react";
import TrailerYt from "components/trailer/TrailerYt";
import ModalBase from "./ModalBase";

const ModalMovie = ({ videos, type, ...props }) => {
  return (
    <>
      <ModalBase {...props}>
        <TrailerYt videos={videos} type={type} />
      </ModalBase>
    </>
  );
};

export default ModalMovie;
